  }
}

.el-footer {
  height: 320px;
  display: flex;
  flex-direction: column;
  font-size: 14px;
  text-align: center;
  justify-content: center;
  align-items: center;

  #f-top {
    display: flex;
    justify-content: space-around;
    margin-bottom: 30px;

    p {
      margin-right: 50px;
    }
  }
}

#h_x {
  width: 100%;
  display: flex;
  height: 100%;
  text-align: center;
  justify-content: space-between;
  align-items: center;
  font-size: 20px;
  color: #fff;

  #title_x {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;

    .p1 {
      display: inline-block;
