      margin-right: 66px;
    }

    .p2 {
      display: inline-block;
      font-size: 26px;
      color: #f97564;
    }

    .t2 {
      text-align: center;
      margin-bottom: 8px;
    }

    .s1 {
      width: 44px;
      height: 30px;
      display: inline-block;
      background: url('./assets/big.png') no-repeat;
