.el-main {
  padding: 0;
  background-color: #fff;
  min-height: 800px;
}
</style>

<template>
  <el-container>
  <toast id="toast" v-show="toastShow" style="z-index:999;top:200px"></toast>
  <el-header style="height:84px" v-if="que">
    <div id='h-left'>
      <span @click="ext()"></span>
      <div id="title">
        <h1 class="t1">逸途-旅游问答社区</h1>
        <p>EASYTO TOURISM KNOWLEDGE</p>
      </div>
    </div>
    <div id="h-right">
      <span id="dh"></span>
      <p>400-826-1977</p>
      <el-menu
        :default-active="activeIndex2"
        class="el-menu-demo"
        mode="horizontal"
        @select="handleSelect"
        background-color="black"
        text-color="#fff"
        active-text-color="#fff" v-if="Login">
        <el-submenu index="1">
          <template slot="title" id="ti">
            <span class="headerImg">
              <img :src="headUrl" />
            </span>
            <p>{{nickname}}</p>
          </template>
          <el-menu-item index="1-1">
            <router-link  class="tab-item" to="/myQuestion">
              <span>我的问答</span>
            </router-link>
          </el-menu-item>
          <el-menu-item index="1-2">
            <router-link  class="tab-item" to="/myEarnings">
              <span>我的收益</span>
            </router-link></el-menu-item>
          <el-menu-item index="1-3">
            <router-link  class="tab-item" to="/feedBack">
              <span>意见反馈</span>
            </router-link></el-menu-item>
          </el-menu-item>
          <el-menu-item index="1-4">
            <router-link  class="tab-item" to="/account">
              <span>账户安全</span>
            </router-link></el-menu-item>
          </el-menu-item>
          <el-menu-item index="1-5" @click="exit">
            <router-link  class="tab-item" to="/">
              <span>退出</span>
            </router-link></el-menu-item>
          </el-menu-item>
        </el-submenu>
      </el-menu>
      <div v-else>
        <router-link  to="/Login">
          <span style="color:#fff">登录</span>
        </router-link>
        <span>|</span>
        <router-link  to="/register">
           <span style="color:#fff">注册</span>
        </router-link>
      </div>
      <span id='bz'></span>
    </div>
  </el-header>
   <el-header style="height:84px" v-else>
    <div id='h_x'>
      <div id="title_x">
        <h2 class="t1">本次你已答题: <p class="p1">67</p></h2>
        <h2 class="t2">预计得到收益: <span class="s1"></span><p class="p2">3553</p></h2>
      </div>
      <h3 @click="ext">退出答题</h3>
    </div>
  </el-header>
  <el-main>
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
  </el-main>
  <el-footer style="height:223px">
    <div id="f-top">
      <p>北京市朝阳区酒仙桥路4号海航大厦</p>
      <p>Tel:(021) 3232-3902</p>
      <p>Fax:(021）54261600</p>
      <p>ZipCode: 200335</p>
    </div>
    <p>Copyright 2016-2019 easyto.com 京ICP备 15037968</p>
  </el-footer>
 </el-container>
</template>

<script>
import toast from './components/toast/toast'
export default {
  components: { toast },
  data() {
    return {
      Login: false,
      activeIndex: '1',
      activeIndex2: '1',
      toastShow: false,
      nickname: '',//用户名
      headUrl: '',//头像
      que: true
    }
  },
  watch: {
    $route: {
      handler(curVal, oldVal) {
        console.log(curVal)
        if(curVal.path === '/quesTion') {
          this.que = false

        } else {
          this.que =true
        }
        if (curVal.query.id == 1) {
          this.Login = true
          this.nickname = localStorage.getItem("nickname")
          this.headUrl = localStorage.getItem("headUrl")
        }

      },
      deep: true
    }
  },
  created() {
    this.loginQue()
    this.initLog()

    if(localStorage.getItem('question') === true){
      console.log('1')
      this.que = false
    }
  },
  beforeRouteLeave (to, from, next) {
    console.log(to,from+'111')
    // 在当前路由改变，但是该组件被复用时调用
    // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
    // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
    // 可以访问组件实例 `this`
    next()
  },
  methods: {

    ext() {
      this.$router.push({ path: '/' });

    },
    handleSelect(key, keyPath) {
      // console.log(key, keyPath)
    },
    // 查询是否是登录状态
    loginQue() {
      if (localStorage.getItem("nickname") && localStorage.getItem("headUrl")) {
        this.Login = true
      } else {
        this.Login = false
      }
    },
    // 获取头像和账号
    // getUser(){
    //   // alert(1)
    //   this.nickname=localStorage.getItem("nickname");
    //   this.headUrl=localStorage.getItem("headUrl");
    // },
    // 退出登录========================================================
    exit() {
      // 调接口----------------
      var _this = this;
      this.$indexServer.exit()
        .then((res) => {
          if (res.data.code == 200) {
            _this.$router.push('/')
            _this.Login = false
            localStorage.clear()
            _this.delCookie('phone')
            _this.delCookie('password')
          } else if (res.data.code == 4010) {
            _this.enterToast('登录已过期，请重新登录', 3000)
            _this.Login = false
            localStorage.clear()
          } else if (res.data.code == 4006) {
            _this.enterToast('该用户账号不存在')
          } else {
            _this.enterToast('服务异常，请稍后再试')
          }
        })
    },
    // 初始化登录状态
    initLog() {
      var _this = this
      //页面初始化时，如果帐号密码cookie存在则填充
      if (this.getCookie('phone') && this.getCookie('password')) {
        // 调接口----------------
        this.$indexServer.loginByPassword(_this.getCookie('phone'), _this.getCookie('password'))
          .then((res) => {
            // console.log(res.data)
            if (res.data.code == 200) {
              _this.$router.push({ path: '/', query: { id: 1 } });
              localStorage.setItem("nickname", res.data.data.nickname);
              localStorage.setItem("headUrl", res.data.data.headUrl);
            }
          })
      }
    },
    // 获取cookie
    getCookie(name) {
      var reg = RegExp(name + '=([^;]+)');
      var arr = document.cookie.match(reg);
      if (arr) {
        return arr[1];
      } else {
        return '';
      }
    },
    // 设置cookie
    setCookie(name, value, day) {
      var date = new Date();
      date.setDate(date.getDate() + day);
      document.cookie = name + '=' + value + ';expires=' + date;
    },
    // 清除cookie
    delCookie(name) {
      this.setCookie(name, null, -1);
    },
    enterToast(text, time) {
      var toast = document.getElementById("toast");
      // console.log(toast)
      if (toast) {
        toast.innerHTML = text;
        this.toastShow = true;
        setTimeout(() => {
          this.toastShow = false;
        }, 2000 || time);
      }
    }

  },

}
</script>



