<template>
  <el-main>
    <div class='waitCheck'>
      <div class='a_lists' v-for='(item,index) in lists' ref='lists'>
        <h1 class='q_title textBig' @click='isShow(index)' v-show='show_a & index === activeIndex'>{{item.question}}<span
          class='q_static'>待审核</span><i class='el-icon-minus up'></i></h1>
        <h1 class='q_title overHiden' @click='isHide(index)' v-show='index !== activeIndex || !show_a'>{{item.question}}<i class='el-icon-plus down'></i></h1>
        <el-collapse-transition>
          <div v-show='show_a & index === activeIndex'>
            <p class='a_descript'>{{item.description}}</p>
            <div class='income'>悬赏积分<em class=gold></em><a class=goldNum>{{item.integralOrg}}</a>
              <div class='source'>来源：{{item.source}}<span>|</span>{{item.createTime}}</div>
            </div>
            <div class='myAnswer'>
              <h2 class='a_top'><i class='lamp'></i>我的回答<span class='a_times'>2017-11-30 12:20:45</span></h2>
              <div :class="isTogel ? 'a_content' : 'a_content hideSome' ">{{item.answer}}</div>
              <el-button type="text" @click='detailTogel'>{{buttonText}}</el-button>
            </div>
          </div>
        </el-collapse-transition>
      </div>
    </div>
    <!--分页-->
    <div class="block">
      <el-pagination layout="prev, pager, next" :total=lists.length :page-size='pageSize'
                     @current-change="handleCurrentChange" :current-page='currentPage'
                     @size-change="handleSizeChange"></el-pagination>
    </div>
  </el-main>
</template>

<script>
  export default {
    data () {
      return {
        activeNames: ['1'],
        buttonText: '显示全文',
        showIndex: 0,
        show_a: false,
        isTogel: false,
        lists: '',
        pageNum: 1,
        pageSize: 20,
        currentPage: 1,
        activeIndex:'',
      }
    },
    mounted () {
      // 初始化
      var _this = this
      _this.getDetail()
     /* _this.isFold()*/
    },
    methods: {
      // 显示隐藏按钮
      isShow (index) {
        this.activeIndex = index
        this.show_a = false
      },
      isHide (index) {
        this.activeIndex = index
        this.show_a = true
      },
      detailTogel () {
        if (this.isTogel === true) {
          this.buttonText = '显示全文'
        } else if (this.isTogel === false) {
          this.buttonText = '收起'
        }
        this.isTogel = !this.isTogel
      },
      getDetail (n) {
          var _this = this
        _this.pageNum =n
        this.$indexServer.nonChecked(_this.pageNum,_this.pageSize)
          .then((res)=>{
            if(res.data.code == 200){
              _this.lists = res.data.data.list
            }
          })
      },
      //判断显示全文按钮是否隐藏
      isFold () {
          let _this = this
        _this.$nextTick(function () {
          /*var arr = document.getElementsByClassName('a_lists')*/
          var arr = _this.$refs.lists
          console.log(arr);
          arr.forEach(function (el, index) {
            if (el.getElementsByClassName('a_content')[0].style.height <= 150) {
              el.getElementsByClassName('el-button')[0].style.display = 'none'
            }
            else {
              el.getElementsByClassName('el-button')[0].style.display = 'block'
            }
          })
        })
      },
      // 分页器
      handleSizeChange: function (size) {
       /* this.pagesize = size;*/
      },
      handleCurrentChange: function(currentPage){
        this.currentPage = currentPage;
        this.getDetail(this.currentPage)
      }
    },
    components: {}
  }
</script>
<style scoped lang="stylus" rel="stylesheet/stylus"></style>



